<template>
  <div>
    <img class="pic" src="../assets/pic/detail.png">
    <Bottom2></Bottom2>
    <transition
	    :enter-class = "$style.enter" 
	    :leave-class = "$style.leave" >
    	<router-view :class="$style.foot"></router-view>
    </transition>
  </div>
</template>

<script>
import Bottom2 from '@/components/Bottom2'

export default {
	data () {
		return {
		}
	},
	components : {
		Bottom2
	}
}
</script>

<style module>
.foot {position: fixed;bottom: 0;width: 100%;height:7.34rem;transition: all .2s;}
.leave {transform: translate3d(0, 0, 0);}
.enter {transform: translate3d(0, 7.34rem, 0);}
</style>



