<template>
	<div class="cf" :class="[$style.bottom, getClz($style,pid)]">
  	<router-link to="/"></router-link>
  	<router-link to="/cate"></router-link>
  	<router-link to="/custom"></router-link>
  	<router-link to="/order"></router-link>
  </div>
</template>

<script>
export default {
	data () {
		return {
			
		}
	},
	props : ['pid'],
	methods : {
		getClz : function(s,p) {
			return s[p]
		}
	}
}
</script>

<style module>
.bottom{width: 100%;background:url(../assets/pic/bottmBar_home.png) no-repeat;background-size:contain;padding:0 10px;position: fixed;bottom: 0;box-sizing: border-box;}
.cate{background-image:url(../assets/pic/bottmBar_type.png)}
.custom{background-image:url(../assets/pic/bottmBar_custom.png)}
.order{background-image:url(../assets/pic/bottmBar_order.png)}
.other{background-image:url(../assets/pic/bottomBar.png)}
.bottom>a{width:25%;height:.92rem;float: left;}
</style>



