import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Custom from '@/pages/Custom'
import Order from '@/pages/Order'
import Cart from '@/pages/Cart'
import Detail from '@/pages/Detail'
import Cate from '@/pages/Cate'
import History from '@/pages/History'
import Search from '@/pages/Search'
import DetailBuy from '@/pages/DetailBuy'
import OrderSure from '@/pages/OrderSure'
import Custom1 from '@/pages/Custom1'
import Custom2 from '@/pages/Custom2'
import Custom3 from '@/pages/Custom3'
import Custom4 from '@/pages/Custom4'
import Custom5 from '@/pages/Custom5'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Index
    },
    {
      path: '/custom',
      component: Custom
    },
    {
      path: '/custom/1',
      component: Custom1
    },
    {
      path: '/custom/2',
      component: Custom2
    },
    {
      path: '/custom/3',
      component: Custom3
    },
    {
      path: '/custom/4',
      component: Custom4
    },

{
      path: '/custom/5',
      component: Custom5
    },

    {
      path: '/cate',
      component: Cate
    },
    {
      path: '/order',
      component: Order
    },
    {
      path: '/order/sure',
      component: OrderSure
    },
    {
      path: '/cart',
      component: Cart
    },
    {
      path: '/detail',
      component: Detail,
      children: [
        { 
          path: 'buy', component: DetailBuy 
        }
      ]
    },
    {
      path: '/history',
      component: History
    },
    {
      path: '/search',
      component: Search
    }
  ]
})



